import React from 'react';
import { Container, VideoBlock, VideoWrapper, Video, Title } from './styled';

const ReelsVideo = ({ title, video }) => {
  return (
    <Container>
      <VideoBlock>
        <VideoWrapper>
          <Video
            muted
            autoPlay
            width="100%"
            height="100%"
            src={video || 'https://i.imgur.com/N2p7W8N.mp4'}
          />
        </VideoWrapper>
      </VideoBlock>

      <Title>{title}</Title>
    </Container>
  );
};

export default ReelsVideo;
