import React from 'react';
import ParagraphCard from 'components/ParagraphCard';
import MoreButton from 'components/MoreButton';
import ButtonSelector from 'components/ButtonSelector';
import ReelsVideo from 'components/ReelsVideo';
import HiddenInput from 'components/HiddenInput';
import {
  Container,
  Wrapper,
  Title,
  TinyText,
  Description,
  TopBlock,
  BottomBlock,
  InfoBlock,
} from 'components/LazypackAttraction/styled';
import {
  SECTION_ID,
  ATTRACTION_LIST,
  ATTRACTION_TAG,
  ATTRACTION_OUTSIDE_URL_MAP,
} from 'constants/index';
import { FormBlock, VideoBlock, ParagraphBlock } from './styled';

const AttractionSection = () => {
  // 選取的標籤
  const [selectedTag, setSelectedTag] = React.useState('');

  const handleSelectorClick = tag => () => {
    setSelectedTag(tag);
  };

  return (
    <Container
      id={SECTION_ID.ATTRACTION}
      style={{ backgroundColor: '#fff2b7' }}
    >
      <Wrapper>
        <HiddenInput id="attractionTags" value={selectedTag} />

        <TopBlock>
          <InfoBlock>
            <TinyText>A reas</TinyText>
            <Title>踩點推薦</Title>
            <Description>來看看推薦景點吧!!</Description>
          </InfoBlock>

          <FormBlock>
            <ButtonSelector
              selected={selectedTag === ATTRACTION_TAG.FOOD}
              onClick={handleSelectorClick(ATTRACTION_TAG.FOOD)}
            >
              FOOD 美食
            </ButtonSelector>
            <ButtonSelector
              selected={selectedTag === ATTRACTION_TAG.SPOT}
              onClick={handleSelectorClick(ATTRACTION_TAG.SPOT)}
            >
              SPOT 景點
            </ButtonSelector>
            <ButtonSelector
              selected={selectedTag === ATTRACTION_TAG.HOTEL}
              onClick={handleSelectorClick(ATTRACTION_TAG.HOTEL)}
            >
              HOTEL 飯店
            </ButtonSelector>
          </FormBlock>
        </TopBlock>

        <BottomBlock>
          <VideoBlock id="attractionVideos">
            <ReelsVideo title="Reels 標題" />
            <ReelsVideo title="Reels 標題" />
          </VideoBlock>

          <MoreButton href="https://www.instagram.com/girlstalk.tw/reels/">
            看更多影片
          </MoreButton>

          <ParagraphBlock id="attractionParagraphs">
            {ATTRACTION_LIST.map(attraction =>
              selectedTag ? (
                attraction.tag === selectedTag && (
                  <ParagraphCard
                    key={attraction.id}
                    link={attraction.link}
                    title={attraction.title}
                    subTitle={attraction.subTitle}
                    image={attraction.image}
                  />
                )
              ) : (
                <ParagraphCard
                  key={attraction.id}
                  link={attraction.link}
                  title={attraction.title}
                  subTitle={attraction.subTitle}
                  image={attraction.image}
                />
              ),
            )}
          </ParagraphBlock>

          <MoreButton
            href={
              ATTRACTION_OUTSIDE_URL_MAP[selectedTag] ||
              ATTRACTION_OUTSIDE_URL_MAP.food
            }
            style={{ marginBottom: 0 }}
          >
            看更多文章
          </MoreButton>
        </BottomBlock>
      </Wrapper>
    </Container>
  );
};

export default AttractionSection;
